.container{
  display: flex;
  justify-content: center;
  background-color: rgb(87, 87, 119);
  border: none;
  border-radius: 25px;
  height: 95vh;
  width: 500px;
  margin: 20px;
}
.mainContainer{
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  width: 400px;
  height: 100vh;
  margin-top: 30px;
}
.topBar{
  margin-top: 10px;
  height: 10px;
  border-radius: 20px;
}
.headerDetails{
  margin-top: 50px;
}
h2{
  margin: 0;
}
h3{
  color: #fff;
  margin: 0;
}
.questionClass{
  padding: 20px;
  background-color:  rgb(1, 1, 45);
  margin-top: 20px;
  margin-bottom: 20px;
  border-radius: 20px;
  height: 240px;
  box-shadow: rgba(137, 138, 139, 0.556) 0px 19px 38px, rgba(121, 122, 122, 0.493) 0px 15px 12px;
}
.questionSection{
  height: 140px;
}
.button-overall{
  display: grid;
  grid-template-columns: 50% 50%;
  grid-template-rows: 50% 50%;
}
button{
  border: none;
  border-radius: 15px;
  cursor: pointer;
  margin: 8px;
  font-weight: bold;
  padding: 8px;
  height: 40px;
  box-shadow: rgba(114, 115, 116, 0.472) 0px 1px 2px 0px, rgba(121, 122, 123, 0.501) 0px 2px 6px 2px;
}
.ansSection{
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.ansReveiw{
  color: #fff;
}
.next-question{
  width: 200px;
}
button:hover{
  background-color:rgba(57, 57, 244, 0.905);
  color: #fff;
}
.chooseButton{
  background-color:rgba(57, 57, 244, 0.905);
  color: #fff;
}
.disabled{
  pointer-events: none;
  cursor: not-allowed;
  opacity: 0.65;
  filter: alpha(opacity=65);
  -webkit-box-shadow: none;
  box-shadow: none;
}